
import { Component, Prop, Vue, Watch } from "vue-property-decorator";
import PoseEditor from "@/components/PoseEditor.vue";
import VelocityEditor from "@/components/VelocityEditor.vue";
import { Plotly } from "vue-plotly";

import { ActiveAgent } from "@/engine/agents";

@Component({
  components: {
    PoseEditor,
    VelocityEditor,
    Plotly
  }
})
export default class ActiveAgentEditor extends Vue {
  @Prop({ type: Object })
    content!: ActiveAgent;

  @Prop({ type: Boolean, default: true })
    enabled!: boolean;

  thrustProfileData: any = [];
  thrustProfileLayout: any = {};
  gimbalProfileData: any = [];
  gimbalProfileLayout: any = {};

  active = false;

  mounted () {
    this.active = this.content.getActive();

    this.thrustProfileData = [{
      x: this.content.thrustProfile.times,
      y: this.content.thrustProfile.values
    }];

    this.thrustProfileLayout = {
      title: {
        text: "Thrust Profile",
        font: {
          size: 12
        }
      },
      margin: {
        autoexpand: false,
        l: 60,
        r: 20,
        t: 20
      },
      yaxis: {
        title: "Thrust [N]",
        showgrid: true,
        zeroline: false,
        showline: false,
        showticklabels: true
      },
      xaxis: {
        title: "Time [s]",
        showgrid: true,
        zeroline: false,
        showline: false,
        showticklabels: true
      },
      height: 200
    };

    this.gimbalProfileData = [{
      x: this.content.gimbalProfile.times,
      y: this.content.gimbalProfile.values
    }];

    this.gimbalProfileLayout = {
      title: {
        text: "Gimbal Profile",
        font: {
          size: 12
        }
      },
      margin: {
        autoexpand: false,
        l: 60,
        r: 20,
        t: 20
      },
      yaxis: {
        title: "Gimbal [Rad]",
        showgrid: true,
        zeroline: false,
        showline: false,
        showticklabels: true
      },
      xaxis: {
        title: "Time [s]",
        showgrid: true,
        zeroline: false,
        showline: false,
        showticklabels: true
      },
      height: 200
    };
  }

  onChangePosition () {
    this.content.syncPosition();
  }

  onChangeActive () {
    this.content.setActive(this.active);
  }

  @Watch("content.active")
  onInputActiveChange () {
    this.active = this.content.getActive();
  }
}
